import { configure, use, run } from 'platojs/system'

import logger from 'modules/logger'
import persist from 'modules/persist'
import request from 'modules/request'
import i18n from 'modules/i18n'
import validator from 'modules/validator'
import config from 'modules/config'
import faq from 'modules/faq'
import demo from 'modules/demo'
import about from 'modules/about'
import user from 'modules/user'
import core from 'modules/core'

import Root from './views/root'

/**
 * 
 */
configure({
  // 
  name: 'PLATO',
  // 
  version: '1.0',
  //  component  element
  element: '#app',
  component: Root
})

/**
 * Use Modules
 */

/**
 * 
 */
__DEV__ && use(logger)

/**
* 
* 
*/
use(request)
use(i18n)
use(validator)

/**
 * 
 */
use(config)
use(user, { prefix: '/' })
use(faq, { prefix: '/' })
use(demo)
use(about)

/**
 * 
 * 
 */
use(core)
use(persist)

/**
 * Run Modules
 */

run(({ router }) => {
  __PROD__ || console.log('%c[PLATO] %cLet\'s go!',
    'font-weight: bold', 'color: green; font-weight: bold')

  /**
   * Let's go!
   */
  router.afterEach(() => {
    //  iOS  BUG
    const activeElement = document.activeElement
    if (activeElement && activeElement.nodeName !== 'BODY') {
      activeElement.blur()
    }
  })
})



