import { configure } from 'platojs/util/request'

/**
 *  request 
 */

export default ({ name, version }, options = {}) => {
  options = { scope: 'request', ...options }

  // 
  configure({
    // configure  mutator 
    //  mutator 
    //  cheap-merge 
    mutator (options) {
      // 
      //  Authorization 
      options.headers['Who-Am-I'] = `${name}@${version}`
      return options
    }
  })

  // 
}



